/****************************************************************************
 * Fonts
 ***************************************************************************/
// Montserrat-Bold
@font-face {
    font-family: "Montserrat";
    src: url("../font/montserrat-bold-webfont.woff2") format("woff2"),
        url("../font/montserrat-bold-webfont.woff") format("woff");
    font-weight: 700;
    font-style: normal;
}
// Montserrat-light
@font-face {
    font-family: "Montserrat";
    src: url("../font/montserrat-light-webfont.woff2") format("woff2"),
        url("../font/montserrat-light-webfont.woff") format("woff");
    font-weight: 300;
    font-style: normal;
}

// SourceSansPro Regular
@font-face {
    font-family: "SourceSansPro";
    src: url("../font/sourcesanspro-regular-webfont.woff2") format("woff2"),
        url("../font/sourcesanspro-regular-webfont.woff") format("woff");
    font-weight: 400;
    font-style: normal;
}

/****************************************************************************
  Body
 ***************************************************************************/
body {
    @extend %fontsize24;
    font-family: $font-family-Montserrat;
    @include bg-img();
    background-attachment: fixed;
}
.container {
    max-width: 1200px;
}
p {
    @extend %fontsize14;
    color: $text-color;
}
ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}
/****************************************************************************
  Links
 ***************************************************************************/
a {
    color: inherit;
    outline: none;
    @include transition(all, 0.2s, ease, 0.2s);
    font-family: $font-primary;
    &:hover,
    &:focus {
        color: $brand-color;
        text-decoration: none;
    }
}
::-moz-selection {
    /* Code for Firefox */
    color: $brand-color;
    background: $brand-color-hover;
}

::selection {
    color: $brand-color;
    background: $brand-color-hover;
}

/****************************************************************************
  Headings
 ***************************************************************************/
h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
    font-family: $font-family-Montserrat;
    font-weight: $bold-weight;
}
h1 {
    @extend %fontsize50;
    color: $brand-color;
    text-transform: uppercase;
}
h2 {
    @extend %fontsize24;
    font-weight: $light-weight;
    color: $brand-color-primary;
    margin-bottom: 20px;
}
