.animate-theme {
  .counter-section {
    padding: 40px 0 30px;
  }
  .text-box {
    margin-top: 40px;
    p {
      color: $white;
      font-weight: $bold-weight;
      letter-spacing: 3px;
      margin-bottom: 30px;
    }
    form {
      max-width: 1024px;
      width: 100%;
      margin: 0 auto;
      .input-group-text {
        background-color: $anim-count-color;
        color: $white;
        border: 0;
        border-radius: 0;
        font-family: $font-primary;
        padding: 10px 35px;
      }
      .input-group {
        .form-control {
          border-radius: 0;
          border: 0;
          height: calc(2.5em + 0.75rem + 2px);
          font-family: $font-primary;
          &:focus {
            box-shadow: none;
          }
        }
      }
    }
  }
  .countdown-style-3 {
    .wrap {
      border: 0;
      background: transparent;
      padding: 0;
    }
    .item-count {
      @extend %fontsize100;
      color: $anim-count-color;
    }
    .item-title {
      color: $white;
    }
  }
}
@include media-breakpoint-down(md) {
  //animate-theme
  .animate-theme {
    .text-box {
      form {
        .input-group-text {
          padding: 0 20px;
        }
        .input-group {
          .form-control {
            height: calc(1.5em + 0.75rem + 2px);
          }
        }
      }
    }
  }
}
