.countdown-container.countdown-style-5 {
    text-align: center; 
    .countdown-days{
        color:$day-progres-color;
    }
    .countdown-hours{
        color:$hours-progres-color;
    }
    .countdown-minutes{
        color:$mins-progres-color;
    }
    .countdown-seconds{
        color:$sec-progres-color;
    }
    .item-title{
        color:$brand-color-primary;
        text-align: center;
        position:absolute;
        @include horizontal-align();
        bottom: -120px;
    }
    .countdown-item {
        height: 307px;
        position: relative;
    }
    .wrap {
        @include horizontal-vertical-align($position: absolute);
        z-index: 10;
    }
    .item-count {
        @extend %fontsize50;
        font-weight:$bold-weight;
        display: block;
        text-align: center;
    }
    .circle-raphael-days,
    .circle-raphael-hours,
    .circle-raphael-minutes,
    .circle-raphael-seconds{        
        @include horizontal-vertical-align($position: absolute);
        z-index: 20;
    }
}
.black-theme{    
    .countdown-container.countdown-style-5{
        .countdown-days{
            color:$day-progres-color;
        }
        .countdown-hours{
            color:$hours-progres-color;
        }
        .countdown-minutes{
            color:$mins-progres-color;
        }
        .countdown-seconds{
            color:$sec-progres-color;
        }
        .item-count{
            font-weight:$light-weight;
        }
        .item-title{
            color:$white;
        }
    }

}
