.countdown-container{
    .countdown-hours,
    .countdown-minutes,
    .countdown-seconds{
        .odometer-inside .odometer-digit:first-child{
            display: none;   
        }
    }
    .odometer{
        &.odometer-theme-minimal{
            &.odometer-animating-up{
                &.odometer-animating{
                    .odometer-ribbon-inner {
                        @include translatey (-100%);
                    }  
                } 
                .odometer-ribbon-inner {
                    -webkit-transition: -webkit-transform 1s;
                    -moz-transition: -moz-transform 1s;
                    -ms-transition: -ms-transform 1s;
                    -o-transition: -o-transform 1s;
                    transition: transform 1s;
                }
            } 
            &.odometer-animating-down{
                &.odometer-animating{
                    .odometer-ribbon-inner {
                        -webkit-transition: -webkit-transform 1s;
                        -moz-transition: -moz-transform 1s;
                        -ms-transition: -ms-transform 1s;
                        -o-transition: -o-transform 1s;
                        transition: transform 1s;
                        @include translatey (0);
                    }  
                } 
                .odometer-ribbon-inner {
                    @include translatey (-100%);
                }

            }
            .odometer-digit {
                display: inline-block;
                vertical-align: middle;
                *vertical-align: auto;
                *zoom: 1;
                *display: inline;
                position: relative;
                .odometer-digit-spacer {
                    display: inline-block;
                    vertical-align: middle;
                    *vertical-align: auto;
                    *zoom: 1;
                    *display: inline;
                    visibility: hidden;
                }
                .odometer-digit-inner {
                    text-align: left;
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    overflow: hidden;
                }
                .odometer-ribbon {
                    display: block;
                }
                .odometer-ribbon-inner {
                    display: block;
                    -webkit-backface-visibility: hidden;
                }
                .odometer-value {
                    display: block;
                    -webkit-transform: translateZ(0);
                    &.odometer-last-value {
                        position: absolute;
                    }
                }
            }
        }
    }
}