.countdown-style-3 {
  .wrap {
    border: 1px solid $white;
    background: $block-bg;
    padding: 70px 0;
  }
  .item-title,
  .item-count {
    display: block;
    text-align: center;
    width: 100%;
  }
  .item-count {
    @extend %fontsize50;
    font-weight: $bold-weight;
    color: $count-color;
    margin-bottom: 10px;
  }
  .item-title {
    text-align: center;
    color: $count-color;
    font-family: $font-primary;
  }
}
@include media-breakpoint-down(lg) {
  .countdown-style-3 {
    .wrap {
      padding: 60px 0;
    }
  }
}
@include media-breakpoint-down(md) {
  .countdown-style-3 {
    .wrap {
      margin-bottom: 20px;
    }
  }
}
@include media-breakpoint-down(sm) {
  .countdown-style-3 {
    .wrap {
      padding: 45px 0;
    }
  }
}
