/*******************************************************************************
* Footer
*******************************************************************************/
footer.page {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  margin-top: 30px;
  .main-footer {
    text-align: center;
    padding: 50px 0 50px;
    .footer-social-link {
      a {
        width: 25px;
        height: 25px;
        position: relative;
        @extend %fontsize14;
        border-radius: 50%;
        @include inline-block();
        margin: 0 8px;
        i.fa {
          position: absolute;
          top: 50%;
          left: 50%;
          @include translate(-50%, -50%);
        }
        &:hover {
          background-color: $brand-color;
          color: $white;
        }
      }
      .tweet {
        background-color: $tweet-color;
      }
      .fb {
        background-color: $fb-color;
      }
      .linkin {
        background-color: $linkedin-color;
      }
      .insta {
        background-color: $insta-color;
      }
      .youtube {
        background-color: $youtube-color;
      }
      .pintrest {
        background-color: $pintrest-color;
      }
      margin-bottom: 30px;
    }
    .copyright {
      p {
        color: $count-color;
        margin: 0;
        a {
          color: $brand-color-hover;
          position: relative;
          &:before {
            content: "";
            width: 0;
            height: 1px;
            background-color: $brand-color-hover;
            position: absolute;
            bottom: 0;
            left: 0;
            @include transition(all, 0.2s, ease, 0s);
          }
          &:hover {
            &:before {
              width: 100%;
            }
          }
        }
      }
    }
  }
}
//Black-theme
.black-theme {
  footer.page {
    .main-footer {
      .footer-social-link {
        a {
          &:hover {
            background-color: $brand-color-hover;
          }
        }
      }
      .copyright {
        p {
          color: $text-color;
        }
      }
    }
  }
}
//Animate-theme
.animate-theme {
  footer.page {
    .main-footer {
      .copyright {
        p {
          color: $white;
        }
      }
    }
  }
}
@include media-breakpoint-between(md, lg) {
  footer.page {
    .main-footer {
      padding: 30px 0 30px;
      .footer-social-link {
        margin-bottom: 30px;
      }
    }
  }
}
@include media-breakpoint-down(lg) {
  footer.page {
    width: 100%;
    position: relative;
  }
}
@include media-breakpoint-down(sm) {
  footer.page {
    .main-footer {
      padding: 20px 0 20px;
      .footer-social-link {
        margin-bottom: 20px;
        a {
          margin: 0 6px 8px;
        }
      }
    }
  }
}
