/*******************************************************************************
* Content Section
*******************************************************************************/
//default code
.content-section {
  padding: 60px 0;
  text-align: center;
  header.heading {
    a {
      margin-bottom: 80px;
      @include inline-block();
    }
    h1 {
      margin-bottom: 30px;
    }
  }
}
//Black-theme code
.black-theme,
.animate-theme {
  .content-section {
    header.heading {
      a,
      h1,
      h2 {
        color: $white;
      }
    }
  }
}
//bgimg-theme code
.bgimg-theme {
  .content-section {
    header.heading {
      a,
      h1,
      h2 {
        color: $count-color;
      }
    }
  }
}
//animate-theme code
.animate-theme {
  .content-section {
    padding: 50px 0 30px;
    header.heading {
      a {
        margin-bottom: 40px;
      }
    }
  }
}

.typo3-messages {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.5);
  .alert.alert-success {
    max-width: 767px;
    width: calc(100% - 30px);
    padding: 30px;
    margin: 00;
    @include horizontal-vertical-align($position: absolute);
    text-align: center;
    box-shadow: 5px 5px 5px 5px rgba(0, 0, 0, 0.2);
    p {
      margin: 0;
      @extend %fontsize14;
    }
    h4 {
      @extend %fontsize24;
    }
    .close-alert {
      position: absolute;
      top: 15px;
      right: 15px;
      @extend %fontsize50;
      line-height: 25px;
    }
  }
}

@include media-breakpoint-between(md, lg) {
  .content-section {
    padding: 50px 0;
    header.heading {
      a {
        margin-bottom: 50px;
      }
      h1 {
        margin-bottom: 20px;
      }
    }
  }
}
@include media-breakpoint-down(sm) {
  .content-section {
    padding: 30px 0;
    header.heading {
      a {
        margin-bottom: 40px;
      }
    }
  }
}
@include media-breakpoint-down(xs) {
  .typo3-messages {
    .alert.alert-success {
      padding: 20px;
      .close-alert {
        top: 5px;
        right: 5px;
      }
    }
  }
}
