//****************************************************************************
//  General extensions
//***************************************************************************/

%inherit_textstyles {
  color: inherit;
  font-size: inherit;
  font-weight: inherit;
  letter-spacing: inherit;
  line-height: inherit;
  text-transform: inherit;
  text-align: inherit;
  font-style: inherit;
}

//****************************************************************************
//  Font sizes extensions
//***************************************************************************/
%fontsize50 {
  font-size: 50px;
  line-height: 69px;
}
%fontsize100 {
  font-size: 100px;
  line-height: 110px;
}
%fontsize24 {
  font-size: 24px;
  line-height: 30px;
}
%fontsize16 {
  font-size: 16px;
  line-height: 20px;
}
%fontsize14 {
  font-size: 14px;
  line-height: 18px;
}
@include media-breakpoint-down(lg) {
  %fontsize50 {
    font-size: 45px;
    line-height: 55px;
  }
}
@include media-breakpoint-between(md, lg) {
  %fontsize50 {
    font-size: 40px;
    line-height: 46px;
  }
  %fontsize100 {
    font-size: 80px;
    line-height: 90px;
  }
  %fontsize24 {
    font-size: 20px;
    line-height: 24px;
  }
}
@include media-breakpoint-down(md) {
  %fontsize50 {
    font-size: 40px;
    line-height: 46px;
  }
  %fontsize100 {
    font-size: 60px;
    line-height: 70px;
  }
  %fontsize24 {
    font-size: 20px;
    line-height: 24px;
  }
  %fontsize16 {
    font-size: 14px;
    line-height: 18px;
  }
}
@include media-breakpoint-down(sm) {
  %fontsize50 {
    font-size: 32px;
    line-height: 36px;
  }
  %fontsize100 {
    font-size: 50px;
    line-height: 60px;
  }
  %fontsize24 {
    font-size: 18px;
    line-height: 22px;
  }
}
